<template>
  <div class="bg">

    <button class="item enable_interactive" v-for="item in lists" @click="clicked(item)">
      {{ item.title }}
    </button>

    <common-third-view
      :searchType=curItem.searchType
      :searchKey="searchKey"
      :demo="demo"
      :desc="desc"
      :detailDesc="detailDesc"
      :rawData="rawData"
    >
    </common-third-view>

  </div>
</template>

<script>
  import CommonThirdView from '../common/CommonThirdView.vue'

  export default {
    name: '',
    components: {
      'common-third-view': CommonThirdView
    },
    data () {
      return {
        lists: [],
        rawData: {},
        curItem: {},
        searchKey: '准备添加...',
        demo: '',
        desc: '',
        demoDesc: '',
        detailDesc: ''
      }
    },
    methods: {
      clicked (item) {
        let data = gddmgr.getSearchDemoData(item.searchType)
        console.log('demoData:' + JSON.stringify(data))
        this.initData(data, item)
//         gddmgr.searchRequest(gconst.SearchType.ZIP_CODE, "610000").then(res => {
//          console.log('res:' + JSON.stringify(res))
//        //  this.initData(data, item)
//             })

        console.log('testSearchKey--------: ', gddmgr.TestSearchKey)
        for (let k in gconst.SearchType) {
          console.log('testSearchKey_params--------: ', gddmgr.TestSearchKey[gconst.SearchType[k]])

          gddmgr.testSearchRequest(gconst.SearchType[k], gddmgr.TestSearchKey[gconst.SearchType[k]]).then(res => {
            console.log('res:' + JSON.stringify(res))
          })
        }
      },

      initData (data, item) {
        this.rawData = data
        this.curItem = item

        this.demo = data.demo || ''
        this.desc = data.desc || ''
        this.detailDesc = data.detailDesc || ''
      }
    },
    created () {
      this.lists = []
      for (let k in gconst.SearchType) {
        let v = gconst.SearchType[k]

        let data = gddmgr.getScreenData(v)
        let title = gddmgr.getScreenTitle(v)
        let o = {
          searchTypeName: k,
          searchType: v,
          title,
          data
        }
        this.lists.push(o)
      }
    }
  }
</script>

<style scoped>
  @import "../../../static/css/main.css";
  @import "../search.css";

  .bg {

    width: 100%;
    height: 100%;
    background: #F0F0F0;
    overflow: auto;    /* 垂直滚动条 */
  }

  .item {
    margin: 10px;
    padding: 10px;
    border-radius: 5px;
  }
 .root {
   padding: 20px 50px 0px;
 }
</style>



